import React from "react";
import "./App.css";
import { useAuth } from "context/AuthContext";
import AuthPage from "pages/authPage";
import UnAutnPage from "pages/unAuthPage";
import { ErrorBoundary, FullError } from "components";
// import TryUseUndo from "tryUseUndo";

function App() {
  const { user } = useAuth();
  return (
    <div className="App">
      <ErrorBoundary fallbackRender={FullError}>
        {user ? <AuthPage /> : <UnAutnPage />}
      </ErrorBoundary>
      {/* <TryUseUndo /> */}
    </div>
  );
}

export default App;
